  .front_footer {
    margin-bottom: 25px;
    width: 277px;
    height: 100px;
  }
  .front_media {
    display: flex;
    flex: 1 1 auto;
    justify-content: center;
    align-items: center;
    /* width: calc(95% - 0.5em);  */
    height: 438px;
    padding: 0.5em;
    background-color: #bde;
    border-radius: 0.5em;
    object-fit: cover;
    vertical-align: middle;
    
  }
  
  .front_media img {
    max-width: none;
    min-width: auto;
    min-height: auto;
    padding: 1.5em;
    margin-top: 5px;
    /* width: calc(95% - -7.5em); */
    height: 117%;
    /* min-width: 200px; */
    width: auto;
  }
  
  .front_body {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .front_body span {
    font-family: "Calligraph";
    
    font-weight: bold;
    padding: 0.5em;
    color: var(--colorText);
    /* background-color: #dbe5dd; */
    background-color: #bda09d;;
    background-image: var(--allBackroundImage);
    /* background-attachment: fixed;
    background-size: cover; */
    font-size: 3em;
    text-align: center;
    border-radius: 1em;
    box-shadow: inset 19px -19px 38px #ef2744, inset -19px 19px 38px #fb879b;
    margin-top: 1.5em;
    text-shadow: var(--textShadow);
    /* box-shadow: var(--boxShadow); */
  
  }